import { render } from "vue";
import { createStore } from "vuex";
import {
  createMap,
  updateLayers,
  changeLayer,
  updateRenderer,
} from "../esriLogic/esriapi";

export default createStore({
  state: {
    view: "",
    censusData: "",
    isModalOpen: false,
    layerSelected: 3,
  },
  getters: {
    getCensusData(state) {
      return state.censusData;
    },
  },
  mutations: {
    setView(state, view) {},
    setCensusData(state, censusData) {
      state.censusData = censusData;
    },
    setLayerSelected(state, layerId) {
      state.layerSelected = layerId;
    },
  },
  actions: {
    changeLayer(context, id) {
      changeLayer(id);
      context.commit("setLayerSelected", id);
    },
    updateRenderer(context, { id, renderer }) {
      updateRenderer(id, renderer);
    },
  },
});
