import MapImageLayer from "@arcgis/core/layers/MapImageLayer";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";
import {
  populationRendererCounties,
  popDensityRendererCounties,
  populationRendererStates,
  populationRendererCensus,
} from "./renderer";

const mapServiceUrl =
  "https://sampleserver6.arcgisonline.com/arcgis/rest/services/Census/MapServer";

// addtional layers
// points to the states layer in a service storing U.S. census data
export const countyFl = new FeatureLayer({
  url: `${mapServiceUrl}/2`,
});

export const censusLayers = new MapImageLayer({
  url: mapServiceUrl,
  opacity: 0.5,
  sublayers: [
    {
      id: 1,
      title: "Census Blocks",
      renderer: populationRendererCensus,
      visible: false,
      source: {
        mapLayerId: 1,
      },
    },
    {
      id: 2,
      title: "Census Counties",
      renderer: populationRendererCounties,
      visible: false,
      source: {
        mapLayerId: 2,
      },
    },
    {
      id: 3,
      title: "Census States",
      renderer: populationRendererStates,
      visible: true,
      source: {
        mapLayerId: 3,
      },
    },
  ],
});
