// Default import
import Map from "@arcgis/core/Map";
import MapView from "@arcgis/core/views/MapView";

const app = {};
import { censusLayers, countyFl } from "./layers";

import {
  populationRendererCounties,
  popDensityRendererCounties,
  populationRendererStates,
  medAgeRendererStates,
  populationRendererCensus,
} from "./renderer";

import store from "../store";

export const createMap = async function() {
  app.map = new Map({
    basemap: "streets", // Basemap layer service
  });
  app.view = new MapView({
    map: app.map,
    center: [-97.805, 38.027], // Longitude, latitude
    zoom: 4, // Zoom level
    container: "map", // Div element
  });

  app.view.on("mouse-wheel", function(evt) {
    // prevents zooming with the mouse-wheel event
    evt.stopPropagation();
  });

  // wait for the map view to finish loading
  app.view.when(function() {
    // add layers to map
    // app.map.add(countyFl); // this feature layer comes from the layers.js file
    app.map.add(censusLayers); // this MapImageLayer comes from the layer.js file

    // register click event on view for map clicks
    app.view.on("click", (e) => {
      queryLayersOnClick(e);
    });
  });
};

export const queryLayersOnClick = function(e) {
  var layer = censusLayers.findSublayerById(
    parseInt(store.state.layerSelected)
  );
  var layerQuery = layer.createQuery();
  layerQuery.geometry = app.view.toMap(e);
  layerQuery.outFields = ["*"];
  layer.queryFeatures(layerQuery).then(function(response) {
    const percentChange = Math.round(
      ((response.features[0].attributes.POP2007 -
        response.features[0].attributes.POP2000) /
        response.features[0].attributes.POP2000) *
        100
    );
    const censusData = {
      state_name: response.features[0].attributes.STATE_NAME,
      county_name: response.features[0].attributes.NAME,
      pop_2007: response.features[0].attributes.POP2007,
      per_increase_2000: percentChange,
      med_age: response.features[0].attributes.MED_AGE,
      avg_fam_size: response.features[0].attributes.AVE_FAM_SZ,
    };
    store.commit("setCensusData", censusData);
  });
};

export const changeLayer = function(id) {
  censusLayers.allSublayers.forEach((layer) => {
    if (layer.id == id) {
      layer.visible = true;
    } else {
      layer.visible = false;
    }
  });
};

export const updateRenderer = function(layerId, renderer) {
  console.log(layerId, renderer);
  const sublayer = censusLayers.findSublayerById(parseInt(layerId));
  if (renderer === "medAgeRendererStates") {
    sublayer.renderer = medAgeRendererStates;
  } else if (renderer === "populationRendererStates") {
    console.log(sublayer, "render states");
    sublayer.renderer = populationRendererStates;
  } else if (renderer === "populationRendererCounties") {
    sublayer.renderer = populationRendererCounties;
  } else if (renderer === "popDensityRendererCounties") {
    sublayer.renderer = popDensityRendererCounties;
  }
};

export const updateOpacity = function(opacity) {
  censusLayers.opacity = opacity / 100;
};
